@charset "UTF-8";
@font-face {
  font-family: "Ubuntu-Light";
  src: url("../font/Ubuntu-Light.woff2") format("woff2"), url("../fonts/Ubuntu-Light.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Ubuntu-Medium";
  src: url("../fonts/Ubuntu-Medium.woff2") format("woff2"), url("../fonts/Ubuntu-Medium.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}
ul {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 0;
}
* {
  box-sizing: border-box;
}
li {
  list-style-type: none;
}
.page {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  height: 100%;
  overflow-x: hidden;
  position: relative;
}
img {
  max-width: 100%;
  height: auto;
  margin: 0;
}
.pseudo-width-background::after {
  content: '';
  display: block;
  position: absolute;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  top: 0;
  left: -50vw;
  right: -50vw;
  margin-right: auto;
  margin-left: auto;
}
.form__input {
  outline: none;
  -webkit-appearance: none;
  font-size: 13px;
  padding: 0;
  margin: 0;
  background-color: transparent;
}
.form__button {
  outline: none;
  -webkit-appearance: none;
  padding: 0;
  margin: 0;
  border: 0;
  background-color: transparent;
  cursor: pointer;
  transition: .3s;
}
/*плейсхолдер*/
::-webkit-input-placeholder {
  color: #ababab;
  opacity: 1;
}
/* webkit */
::-moz-placeholder {
  color: #ababab;
  opacity: 1;
}
/* Firefox 19+ */
:-moz-placeholder {
  color: #ababab;
  opacity: 1;
}
/* Firefox 18- */
:-ms-input-placeholder {
  color: #ababab;
  opacity: 1;
}
/* IE */
:focus::-webkit-input-placeholder {
  opacity: 0;
}
/* webkit */
:focus::-moz-placeholder {
  opacity: 0;
}
/* Firefox 19+ */
:focus:-moz-placeholder {
  opacity: 0;
}
/* Firefox 18- */
:focus:-ms-input-placeholder {
  opacity: 0;
}
/* IE */
.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}
.link {
  text-decoration: none;
  font-size: 14px;
  cursor: pointer;
  font-weight: 400;
  transition: .3s;
  color: #393939;
}
.select-box {
  width: 90px;
  position: relative;
  height: 35px;
  background-color: #393939;
  color: #ffffff;
  transition: .3s;
}
.select-box .select-box__value-tag {
  transition: .2s;
  height: 35px;
}
.select-box__value-tag {
  display: inline-block;
  margin: 0 0 0 12px;
  height: 100%;
  cursor: default;
  font-family: "Ubuntu-Light";
  font-style: normal;
  font-weight: 400;
  text-transform: uppercase;
  font-size: 11px;
}
.select-box__value-tag::before {
  content: ' ';
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}
.arrow-select-link {
  display: flex;
  cursor: pointer;
  align-items: center;
  position: absolute;
  left: 0;
  width: 100%;
  height: 35px;
  color: #ffffff;
  justify-content: flex-end;
}
.arrow-select {
  margin: auto;
  width: 25px;
  height: 100%;
  position: absolute;
  right: 0;
}
.arrow-select:after {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  border: 6px solid transparent;
  border-top: 5px solid #ffffff;
  top: 16px;
  left: 0;
  right: 0;
  margin: auto;
}
.select-menu-box {
  background: #ffffff;
  transition: .3s;
  width: 100%;
  height: 0;
  top: 35px;
  overflow: hidden;
  position: absolute;
  z-index: 10;
}
.option {
  width: 100%;
  height: auto;
}
.option-link {
  display: block;
  width: 100%;
  padding: 10px 0 10px 16px;
  cursor: pointer;
  color: #888888;
  background-color: #ffffff;
  font-family: "Lato Bold";
  font-style: normal;
  font-weight: 400;
  font-size: 11px;
  text-transform: uppercase;
  transition: .3s;
}
.option-link:link {
  color: #888888;
  background-color: #ffffff;
}
.option-link:visited {
  color: #888888;
  background-color: #ffffff;
}
.option-link:hover {
  color: #393939;
  background: #d9bf8f;
}
.option-link:focus {
  color: #888888;
  background-color: #ffffff;
}
.option-link:active {
  color: #888888;
  background-color: #ffffff;
}
